@import "../../mixin";
.catalog-tabs{
    display: flex;
    &__li{
        padding: 12px 30px 11px 75px;
        text-align: center;
        border: 1px solid #AAB2B2;
        border-right: 0;
        position: relative;
        cursor: pointer;
        text-transform: uppercase;
        font-weight: 500;
        color: #2B2D2D;
        font-size: 14px;
        letter-spacing: -0.02em;
        &::before{
            content: '';
            @include triangle(12, 7, transparent, top);
            position: absolute;
            left: calc(50% - 6px);
            bottom: 0;
            transition: .2s;
        }
        &:first-child{
            border-top-left-radius: 2px;
            border-bottom-left-radius: 2px;
        }
        &:last-child{
            border-top-right-radius: 2px;
            border-bottom-right-radius: 2px;
            border-right: 1px solid #AAB2B2;
        }
        .sprite-svg{
            position: absolute;
            top: 10px;
            left: 30px;
            fill: #2B2D2D;
            transition: 0.3s;
        }
        &:hover{
            .sprite-svg{
                fill: #F74B4B;
            }
        }
    }
    .active{
        border: 1px solid #4D5151;
        color: #F74B4B;
        .sprite-svg{
            fill: #F74B4B;
        }
        &::before{
            @include triangle(12, 7, #F74B4B, top);
        }
    }
}

